<template>
  <div class="container-fluid user-box">
    <router-link to="/upload/avatar">
      <img v-if="self.avatar" class="img-thumbnail avatar" :src="self.avatar" />
      <img v-else class="img-thumbnail avatar" src="../assets/img/default.jpg" />
    </router-link>

    <div class="info">
      <h5></h5>
      <table class="table">
        <tbody>
          <tr>
            <th></th>
            <td>{{ self.nickname }}</td>
          </tr>
          <tr>
            <th></th>
            <td>{{ self.phonenum }}</td>
          </tr>
          <tr>
            <th></th>
            <td>{{ self.gender == "male" ? "" : "" }}</td>
          </tr>
          <tr>
            <th></th>
            <td>{{ self.birthday }}</td>
          </tr>
          <tr>
            <th></th>
            <td>{{ self.location }}</td>
          </tr>
        </tbody>
      </table>
    </div>

    <div v-if="profile_btn">
      <button @click="show_more()" class="btn btn-success col-10 btn-txt"></button>
    </div>

    <div v-else class="info">
      <h5></h5>
      <table class="table">
        <tbody>
          <tr>
            <th></th>
            <td>{{ self.profile.dating_gender == "male" ? "" : "" }}</td>
          </tr>
          <tr>
            <th></th>
            <td>{{ self.profile.dating_location }}</td>
          </tr>
          <tr>
            <th></th>
            <td>{{ self.profile.max_dating_age }} </td>
          </tr>
          <tr>
            <th></th>
            <td>{{ self.profile.min_dating_age }} </td>
          </tr>
          <tr>
            <th></th>
            <td>{{ self.profile.max_distance }} </td>
          </tr>
          <tr>
            <th></th>
            <td>{{ self.profile.min_distance }} </td>
          </tr>
          <tr>
            <th></th>
            <td>{{ self.profile.auto_play ? "" : "" }}</td>
          </tr>
          <tr></tr>
          <tr>
            <th></th>
            <td>{{ self.profile.only_matched ? "" : "" }}</td>
          </tr>
          <tr>
            <th></th>
            <td>{{ self.profile.vibration ? "" : "" }}</td>
          </tr>
        </tbody>
      </table>
    </div>

    <router-link v-if="!profile_btn" to="/user/update" class="btn btn-primary col-10 btn-txt"></router-link>
  </div>
</template>

<script>
import request from "../libs/http";
import { Profile } from "../libs/models";

export default {
  name: "UserShow",
  props: ["self"],

  data: function() {
    return {
      profile_btn: true,
    };
  },

  methods: {
    show_more() {
      if (this.self.profile.dating_gender === undefined) {
        console.log(" profile");
        request.get("/api/user/profile/show").then((response) => {
          let profile = new Profile(response.data.data);
          this.self.set_profile(profile);
        });
      }
      this.profile_btn = false;
    },
  },
};
</script>

<style></style>
